.home {
  .homeContainer {
    padding: 4rem 1.5rem;
    .introText {
      font-size: 1.3rem;
      color: var(--primaryColor);
      font-weight: 600;
      letter-spacing: 1px;
    }
    .title {
      font-size: 3.3rem;
      font-weight: 600;
      color: var(--textColor);
      padding: 0.7rem 0;
    }
    .subTitle {
      display: block;
      font-size: 1.8rem;
      font-weight: 600;
      color: var(--secondaryColor);
      line-height: 2rem;
      padding: 0.5rem 0;
    }

    .homeParagraph {
      font-size: 1.3rem;
      padding: 0.6rem 1rem;
      color: var(--paragrapColor);
      padding: 1rem 0;
      max-width: 600px;
    }

    .lowerHomeSection {
      .contactBtn {
        padding: 0.6rem 1rem;
        background: var(--primaryColor);
        color: var(--BgColor);
        border: none;
        outline: nine;
        &:hover {
          background: var(--primaryHover);
        }

        a {
          font-size: 18px;
          font-weight: 600;
          gap: 0.5rem;
          align-items: center;
          color: var(--BgColor);
        }
      }

      .scrollDiv {
        padding-top: 1rem;
        display: flex;
        align-items: center;
        .scroll {
          font-size: 1rem;
          color: var(--primaryColor);
        }
        .icon {
          font-size: 1.3rem;
          margin-left: 0.6rem;
          color: var(--primaryColor);
          animation: move 0.5s infinite;
        }
        @keyframes move {
          0% {
            transform: translateY(5px);
          }
          25% {
            transform: translateY(0px);
          }
          50% {
            transform: translateY(0px);
          }
          75% {
            transform: translateY(0px);
          }
          100% {
            transform: translateY(5px);
          }
        }
      }
    }
  }
  .leftIcons,
  .rightEmail {
    display: none;
  }
}

//Media Queris

@media screen and (min-width: 480px) {
  .home {
    .homeContainer {
      padding: 5rem 1.5rem;
      .introText {
        font-size: 1.5rem;
      }
      .title {
        font-size: 3.5rem;
      }
      .subTitle {
        font-size: 2.1rem;
      }
      .homeParagraph {
        font-size: 1.5rem;
      }

      .lowerHomeSection {
        .contactBtn {
          padding: 0.8rem 1.5rem;
          a {
            font-size: 22px;
          }
        }
      }
      .scrollDiv {
        padding-top: 1.5rem;
        .icon {
          font-size: 1.5rem;
        }
      }
    }
  }
}

@media screen and (min-width: 770px) {
  .home {
    .homeContainer {
      padding: 5rem 1.5rem;
      margin: auto;
    }
    .leftIcons,
    .rightEmail {
      display: block;
      position: fixed;
      .icon {
        color: var(--secondaryColor);
      }
      a {
        font-weight: 600;
        color: var(--secondaryColor);
      }

      .line {
        margin: auto;
        content: "";
        background: var(--secondaryColor);
        height: 100px;
        width: 1px;
      }
    }
    .leftIcons {
      left: 5%;
      bottom: 0;
      .socials {
        gap: 1rem;
        a {
          color: var(--textColor);
          &:hover {
            color: var(--primaryColor);
          }
        }
      }
    }
    .rightEmail {
      right: 0;
      bottom: 0;
      .emailAddress {
        transform: rotate(90deg) translate(-90px, 0px);
        a {
          letter-spacing: 1px;
          color: var(--textColor);
        }
      }
    }
  }
}

@media screen and (min-width: 1240) {
  .home {
    .homeContainer {
      padding: 6rem;
      .introText {
        font-size: 1.5rem;
      }
      .title {
        font-size: 5rem;
      }
      .subTitle {
        line-height: 2.5rem;
        font-size: 2.4rem;
      }
      .homeParagraph {
        font-size: 1.8rem;
      }
    }
  }
}
