@import url("https://fonts.googleapis.com/css2?family=Advent+Pro:ital,wght@0,300;0,400;1,700&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Advent Pro", sans-serif;
  transition: 0.3s ease;
}

:root {
  --primaryColor: #00eaff;
  --primaryHover: hsl(185, 92% 41%);
  --secondaryColor: rgb(150, 162, 188);
  --paragraphColor: rgb(127, 127, 175);
  --BgColor: #031a30;
  --formColor: #031a300;
  --formColor: rgb(2, 15, 29);
  --projectCardColor: #072340;
  --textColor: #dae4fb;
  --headerHeight: 3rem;
}

html {
  scroll-behavior: smooth;
}
a {
  text-decoration: none;
}
li {
  list-style: none;
}
.flex {
  display: flex;
  align-content: center;
}
.grid {
  display: grid;
  align-content: center;
}

input,
textarea {
  font-size: 100%;
}
.section {
  padding: 4rem 0 2rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

img {
  height: auto;
  width: 100%;
}
.icon {
  font-size: 25px;
  cursor: pointer;
}
.btn {
  border: 2px solid var(--primaryHover);
  padding: 0.5rem 1.5rem;
  font-size: 1.1rem;
  font-weight: 600;
  position: relative;
  color: var(--primaryColor);
  background: transparent;
}
.btn::before {
  content: "";
  height: 100%;
  width: 0%;
  background: var(--primaryColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: 0.2s;
}
.btn::after {
  content: "";
  height: 100%;
  width: 0%;
  background: var(--BgColor);
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  transition: 0.5s;
}

.btn:hover::before,
.btn:hover::after {
  width: 100%;
}
.navlink {
  position: relative;
}
.navlink::before {
  content: "";
  position: absolute;
  background: var(--primaryHover);
  height: 2px;
  width: 0%;
  bottom: -3px;
  left: 0;
  transition: 0.3s;
}

.navlink::after {
  content: "";
  position: absolute;
  background: var(--paragraphColor);
  height: 2px;
  width: 0%;
  bottom: -3px;
  left: 0;
  transition: 0.8s;
}

/* Section Title */
.sectionTitle {
  padding: 1rem 0;
  font-size: 2rem;
  color: (--textolor);
  display: flex;
  align-items: center;
}
.titlenumber {
  color: var(--primaryColor);
  margin: 0 0.6rem;
  font-weight: 600;
}
.titleText {
  position: relative;
  width: max-content;
}
.underline {
  position: absolute;
  width: 100%;
  height: 3px;
  top: 35px;
  left: 0;
  border-radius: 10px;
  background: var(--secondaryColor);
  overflow: hidden;
}
.underline span {
  top: 0;
  left: 10px;
  height: 100%;
  width: 10px;
  border-radius: 10px;
  position: absolute;
  background: var(--textColor);
  animation: moveLine 2s linear infinite;
}

@keyframes moveline {
  0% {
    left: -20;
  }
  100% {
    left: 100px;
  }
}

/* Body styling*/

body {
  background: var(--BgColor);
  color: white;
}

::selection {
  background-color: var(--primaryColor);
  color: white;
}

::-webkit-scrollbar {
  width: 10px;
  background: var(--projectCardColor);
}
::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background: var(--primaryHover);
}
@media screen and (min-width: 769px) {
  .container {
    width: 70%;
    margin: auto;
  }
}

@media screen and (min-width: 960px) {
  .container {
    width: 70%;
    margin: auto;
  }
}
@media screen and (min-width: 1240px) {
  .section {
    padding: 6rem 0 4rem;
  }
  .container {
    width: 70%;
    margin: auto;
  }
}
@media screen and (min-width: 1440px) {
  body {
    zoom: 1;
  }
}
@media screen and (min-width: 2560px) {
  body {
    zoom: 1.7;
  }
}
